import "deps/phoenix_html/web/static/js/phoenix_html"

// Import local files
//
// Local files can be imported directly using relative
// paths "./socket" or full ones "web/static/js/socket".

import socket from "./socket"

let PostList = React.createClass({
  getInitialState() {
    return {
      posts: []
    }
  },
  componentDidMount() {
    $.ajax({
      url: '/api/posts',
      dataType: 'json',
      cache: false,
      success: function(data) {
        this.setState({posts: data.data});
      }.bind(this),
      error: function(xhr, status, err) {
        console.log(`${status}: ${err.toString()}`)
      }
    });
    this.subscribeToNewPosts();
  },
  subscribeToNewPosts() {
    this.props.socket.connect();
    this.props.channel.on("new:post", post => {
      this.injectNewPost(post);
      console.log(post);
    });
  },
  injectNewPost(post) {
    this.setState({
      posts: [post].concat(this.state.posts)
    });
  },
  pushNewPost(post, channel) {
    channel.push('new:post', post);
  },
  handleSubmit(e) {
    e.preventDefault();

    var title = $('.title').val().trim();
    var body = $('.body').val().trim();

    var post = {
      title: title,
      body: body
    }

    $.ajax({
      url: '/api/posts',
      dataType: 'json',
      type: 'POST',
      data: {post},
      success: function(data) {
        this.pushNewPost(data.data, this.props.channel);
        console.log("Successfully created post!");
      }.bind(this),
      error: function(xhr, status, err) {
        console.log(`${status}: ${err.toString()}`);
      }.bind(this)
    });
  },
  render() {
    return (
      <div>
        <form onSubmit={this.handleSubmit}>
        <div className="form-group">
          <label htmlFor="title">Title</label>
          <input type="text" className="form-control title" />
        </div>
        <div className="form-group">
          <label htmlFor="body">Body</label>
          <input type="text" className="form-control body" />
        </div>
        <input type="submit" className="btn btn-default" />                   
        </form>
        <ul className="list-group">
        { this.state.posts.map(post => { return <Post post={post} key={post.id} /> }) }
        </ul>
          
      </div>
    )
  }
});

let Post = React.createClass({
  handleDelete(e) {
    e.preventDefault();

    var url = `/api/posts/${this.props.post.id}`;

   $.ajax({
     url: url,
     type: 'DELETE',
     success: function() {
       console.log("Successfully deleted post!");
     }.bind(this),
     error: function() {
       console.log("Error deleting post");
     }.bind(this)
   }); 
  },
  render() {
    return (
      <li className="list-group-item">{this.props.post.title} <a href="#" onClick={this.handleDelete}>X</a></li>
    )
  }
});

window.onload = () => {
  let element = document.getElementById("app");
  let channel = socket.channel("posts:new", {});
  channel.join().receive("ok", chan => {
      console.log("joined");
  });
  React.render(<PostList socket={socket} channel={channel}/>, element)
}
